import React from 'react';
import './CreateAccount.css';  // Import your CSS for styling

const CreateAccount = () => {
  return (
    <div className="create-account-wrapper">
      <div className="create-account-container">

        {/* Left Section with Create Account Form */}
        <div className="signup-form-container">
          <h1 className="signup-title">Create an Account</h1>
          <p className="signup-subtitle">Fill in the details below to sign up</p>

          <form className="signup-form">
            {/* Name Input */}
            <div className="form-group">
              <label htmlFor="name">Full Name</label>
              <input type="text" id="name" placeholder="Enter your full name" required />
            </div>

            {/* Email/Phone Input */}
            <div className="form-group">
              <label htmlFor="emailOrPhone">Email or Phone Number</label>
              <input type="text" id="emailOrPhone" placeholder="Enter your email or phone number" required />
            </div>

            {/* Password Input */}
            <div className="form-group">
              <label htmlFor="password">Password</label>
              <input type="password" id="password" placeholder="Create a password" required />
            </div>

            {/* Sign up Button */}
            <button type="submit" className="signup-button">Create Account</button>

            {/* Google Signup Button */}
            <button type="button" className="google-signup">
              <img src="https://upload.wikimedia.org/wikipedia/commons/5/53/Google_%22G%22_Logo.svg" alt="Google Logo" className="google-icon" />
              Sign up with Google
            </button>
          </form>

          <p className="login-link">
            Already have an account? <a href="/login">Log in</a>
          </p>
        </div>

        {/* Right Section with Background Image */}
        <div className="signup-image-container">
          <img src="path/to/your-background-image.jpg" alt="Signup Background" className="signup-image" />
        </div>

      </div>
    </div>
  );
};

export default CreateAccount;
